<template>
  <div >
    <b-button @click="goToBack" size="md" class="mb-2 goBack">
      <b-icon icon="arrow90deg-left" aria-hidden="true"></b-icon> Back
    </b-button>
    <b-table
      ref="tableRef"
      id="keyTable"
      :busy="false"
      no-border-collapse
      bordered
      hover
      responsive
      show-empty
      :sticky-header="clientHeight"
      :items="shemaTable"
      :fields="fields"
      :row-click-handler="handleRowFunction"
      @scroll.native="onScroll"
    >
      <template #table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
          <strong>Loading...</strong>
        </div>
      </template>
      <template #cell()="data">
        <div
          v-if="
            data.item.options.editMode === true && data.field.key === editCell
          "
        >
        <!-- v-model="valueCell" -->
          <input
            :value="valueCell"
            @input="updateValueCell"
            v-on:keyup.enter="handleBlur()"
            v-on:keyup.46="handleBlur('delete')"
            v-autofocus
            @blur="handleBlur()"
          />
        </div>
        <div v-else>
          <span
            :class="data.value === '___' ? 'disableEmpty' : ''"
            @click="handleRow(data)"
            >{{ data.value }}</span
          >
        </div>
      </template>
    </b-table>
  </div>
</template>

<script>
/* eslint-disable no-restricted-syntax */
/* eslint-disable no-console */

import Vue from 'vue';
import { mapState, mapActions } from 'vuex';

import autofocus from 'vue-autofocus-directive';

Vue.directive('autofocus', autofocus);

export default {
  data() {
    return {
      clientHeight: `${document.documentElement.clientHeight - 120}px`,
      pathPrams: '',
      cloneObjArrEmptyStrings: [],
      handleRowFunction: this.handleRow,
      editCell: '',
      payloadData: '',
      payloadConfig: '',
      startPositionRow: 1,
      countRow: 100,
      maxHeightContent: 5000,
      bankScrollTop: 0,
      fakeScrollTop: 0,
      arrEmptyStrings: [],

    };
  },
  computed: {
    ...mapState({
      dataBE: (state) => state.dataBE,
      fields: (state) => state.fields,
      items: (state) => state.items,
      valueCell: (state) => state.valueCell,
      shemaTable: (state) => state.shemaTable,

    }),
  },
  async mounted() {
    try {
      this.pathPrams = window.location.pathname.split('/');
      this.payloadData = JSON.stringify({
        start_col: 'A',
        start_row: '1',
        end_col: 'AAAA',
        end_row: '100',
      });

      await this.handleGeneralData(this.payloadData);

      const arrShema = [];
      for (let ind = this.startPositionRow; ind < this.countRow; ind += 1) {
        this.cloneObjArrEmptyStrings = JSON.parse(JSON.stringify(this.arrEmptyStrings));
        this.cloneObjArrEmptyStrings._ = ind;
        arrShema.push(this.cloneObjArrEmptyStrings);
      }
      this.getShemaTable(arrShema);
      this.items.forEach((item, index) => {
        this.shemaTable.forEach((val, i) => {
          if (item._ === val._) {
            for (const key in this.shemaTable[i]) {
              if ({}.hasOwnProperty.call(this.shemaTable[i], key)) {
                for (const emptyKey in item) {
                  if (emptyKey === key) {
                    this.getOneObjShemaTable({
                      i,
                      key,
                      item,
                    });
                  }
                }
              }
            }
          }
        });
        if (index > 50) return false;
        return true;
      });
      const newArrForShema = this.shemaTable.map((item) => {
        const itemField = item;
        itemField.options = { editMode: false, key: Math.random() };
        return itemField;
      });
      this.getShemaTable(newArrForShema);
    } catch (err) {
      console.log(err, 'err-mounted');
    }
  },
  methods: {
    ...mapActions(['getDataCellsFromBE', 'getFields', 'getItems', 'getValueCell', 'getShemaTable', 'getOneObjShemaTable']),
    handleRow(cell) {
      this.getValueCell('');
      if (cell.value !== '___') this.getValueCell(cell.value.trim());
      this.shemaTable.forEach((item, index) => {
        if (item._ === cell.item._ && cell.field.key !== '_') {
          this.editCell = cell.field.key;
          this.currentRow = index;
          this.currentCow = cell.field.key;

          const newArrForShema = this.shemaTable.map((foo) => {
            const itemField = foo;
            itemField.options.editMode = false;
            return itemField;
          });
          this.getShemaTable(newArrForShema);

          this.shemaTable[index].options.editMode = true;
          this.shemaTable[index][cell.field.key].toString()
            .substring(0, this.shemaTable[index][cell.field.key].length - 1);
        }
      });
    },
    async handleGeneralData(payloadData) {
      this.payloadConfig = {
        method: 'post',
        url: `/table/${this.pathPrams[2]}/editor/get`,
        headers: {
          'Content-Type': 'application/json',
        },
        data: payloadData,
      };
      await this.getDataFromBe(this.payloadConfig);
      const arrItem = [];
      console.log(this.dataBE, 'this.dataBE');
      if (!!this.dataBE && !!this.dataBE.length) {
        this.dataBE.forEach((dat) => {
          arrItem.push({
            _: dat.r,
            [dat.c]: dat.v,
          });
        });
      }
      console.log(arrItem, '<<<<<<<<======arrItem');
      this.getItems(arrItem.length ? arrItem : []);
      const keysLabel = [];
      this.items.forEach((val) => {
        const cloneVal = JSON.parse(JSON.stringify(val));
        delete cloneVal.options;
        keysLabel.push(...Object.keys(cloneVal));
      });
      const cloneFields = JSON.parse(JSON.stringify(this.fields));
      keysLabel.push(...cloneFields);
      let uniqueKeys = new Set(keysLabel);
      uniqueKeys = [...uniqueKeys].sort();
      uniqueKeys.splice(uniqueKeys.length - 2, 2);
      uniqueKeys = [...uniqueKeys].sort((a, b) => {
        if (a.length < b.length) return -1;
        return 1;
      });
      this.getFields([this.fields[0], ...uniqueKeys]);
      this.arrEmptyStrings = [];
      this.fields.forEach((field, index) => {
        if (index > 0) {
          this.arrEmptyStrings.push({
            [field]: '___',
          });
        }
      });
      this.arrEmptyStrings = this.arrEmptyStrings.reduce((acc, item) => {
        for (const key in item) {
          if ({}.hasOwnProperty.call(item, key)) {
            acc[key] = item[key];
          }
        }
        return acc;
      }, {});
    },

    async handleBlur(caseButton) {
      const newArrForShema = this.shemaTable.map((item) => {
        const itemField = item;
        itemField.options.editMode = false;
        return itemField;
      });
      this.getShemaTable(newArrForShema);
      const beforeUpdateCell = this.shemaTable[this.currentRow][
        this.currentCow
      ];
      let resaltSum = null;
      if (this.valueCell[0] === '=') {
        const operation = this.valueCell.split('=').pop();
        const operationMath = operation.split('(')[0];

        if (operationMath === 'СУММ' || operationMath === 'СРЗНАЧ') {
          resaltSum = await this.mathematicalCalculations(
            operation,
            operationMath,
            resaltSum,
          );
        }
      }

      if (resaltSum === null) {
        if (beforeUpdateCell.toString() !== '___') {
          if (this.valueCell) {
            this.shemaTable[this.currentRow][this.currentCow] = this.valueCell;
          }
        } else if (this.valueCell) {
          this.shemaTable[this.currentRow][this.currentCow] = this.valueCell;
        }
        if (!this.valueCell) this.shemaTable[this.currentRow][this.currentCow] = '___';
      } else {
        this.shemaTable[this.currentRow][this.currentCow] = resaltSum;
      }
      let finalValue;
      if (caseButton === 'delete') {
        finalValue = '___';
      } else if (resaltSum === null) {
        finalValue = this.valueCell;
      } else {
        finalValue = resaltSum;
      }
      if (caseButton === 'delete') this.getValueCell('');

      console.log(finalValue, 'finalValue===>>>');
      const data = JSON.stringify({
        col: this.currentCow,
        row: this.currentRow + 1,
        value: finalValue,
      });
      const config = {
        method: 'post',
        url: `/table/${this.pathPrams[2]}/editor/set`,
        headers: {
          'Content-Type': 'application/json',
        },
        data,
      };
      this.updateCell(config);
    },
    findValueCell(address) {
      let foundPos = -1;
      for (const char of address) {
        if (!Number.isNaN(+char)) {
          foundPos = address.indexOf(char);
        }
      }
      const cellCoordinates = this.splitByIndex(address, foundPos);
      const valueOneCell = this.shemaTable[
        cellCoordinates.row === 0 ? cellCoordinates : cellCoordinates.row - 1
      ][cellCoordinates.cow];
      return valueOneCell;
    },
    splitByIndex(value, index) {
      return {
        cow: value.substring(0, index),
        row: value.substring(index),
      };
    },
    addArrValueCells(oper) {
      let startCell = oper.split(':')[0];
      let finishCell = oper.split(':')[1];
      let foundPos = -1;
      for (const char of startCell) {
        if (!Number.isNaN(+char)) {
          foundPos = startCell.indexOf(char);
        }
      }
      startCell = this.splitByIndex(startCell, foundPos);
      finishCell = this.splitByIndex(finishCell, foundPos);

      const cloneArr = JSON.parse(JSON.stringify(this.shemaTable));
      let findRange = cloneArr.slice(startCell.row - 1, finishCell.row);
      findRange = findRange
        .map((item) => item[startCell.cow])
        .filter((val) => !Number.isNaN(+val));
      return findRange;
    },
    mathematicalCalculations(operation, operationMath, resaltSum) {
      let variableResaltSum = resaltSum;

      try {
        let operationData = operation.split('(')[1];
        operationData = operationData.substring(0, operationData.length - 1);
        const arrOperands = operationData.split(';').filter((oper) => !!oper);
        const arrResaltoperand = [];
        let numberOfcells = 0;

        arrOperands.forEach((itemOperand) => {
          const rangeFoundItemOperand = itemOperand.indexOf(':');
          let valItemOperandCell = null;
          let valueRangeItemOperand = null;
          if (rangeFoundItemOperand !== -1) {
            valueRangeItemOperand = this.addArrValueCells(itemOperand);
            numberOfcells += valueRangeItemOperand.length;
          } else {
            valItemOperandCell = this.findValueCell(itemOperand);
            if (!Number.isNaN(+valItemOperandCell)) {
              numberOfcells += 1;
            }
          }
          let finalOperandItem = 0;
          if (valueRangeItemOperand === null) {
            finalOperandItem = (Number.isNaN(+valItemOperandCell) ? 0 : +valItemOperandCell);
          } else {
            finalOperandItem = valueRangeItemOperand.reduce(
              (accumulator, a) => accumulator + (Number.isNaN(+a) ? 0 : +a),
              0,
            );
          }
          arrResaltoperand.push(Number.isNaN(+finalOperandItem) ? 0 : finalOperandItem);
        });

        if (operationMath === 'СУММ') {
          variableResaltSum = arrResaltoperand.reduce((accumulator, a) => accumulator + +a, 0);
          return variableResaltSum;
        }
        if (operationMath === 'СРЗНАЧ') {
          variableResaltSum = (arrResaltoperand
            .reduce((accumulator, a) => accumulator + +a, 0) / numberOfcells).toFixed(3);
          return variableResaltSum;
        }
        return 'Unknown math operation';
      } catch (err) {
        console.log(err, 'err===>>>');
        variableResaltSum = null;
        return variableResaltSum;
      }
    },
    async getDataFromBe(config) {
      try {
        const response = await Vue.axios(config);
        console.log(response.data, '<<<<<<<<<<<<<<========response.data');
        this.getDataCellsFromBE(response.data);
      } catch (err) {
        console.log(err, '<<<<<<<<======error');
      }
      console.log(this.dataBE, '<<<<<<<<<<<<<<========this.dataBE');
    },
    async updateCell(config) {
      try {
        const response = await Vue.axios(config);
        console.log(response, '<<<<<<<<<<<<<<========response-update');
      } catch (err) {
        console.log(err, '<<<<<<<<======error-update');
      }
    },
    updateValueCell(e) {
      this.getValueCell(e.target.value.trim());
    },
    goToBack() {
      window.location.href = '/';
    },
    updateShemaData(arrShema) {
      this.getShemaTable(arrShema);
      this.items.forEach((item) => {
        this.shemaTable.forEach((val, i) => {
          if (item._ === val._) {
            for (const key in this.shemaTable[i]) {
              if ({}.hasOwnProperty.call(this.shemaTable[i], key)) {
                for (const emptyKey in item) {
                  if (emptyKey === key) {
                    this.getOneObjShemaTable({
                      i,
                      key,
                      item,
                    });
                  }
                }
              }
            }
          }
        });
      });
      const newArrForShema = this.shemaTable.map((item) => {
        const itemField = item;
        itemField.options = { editMode: false, key: Math.random() };
        return itemField;
      });
      this.getShemaTable(newArrForShema);
    },

    async onScroll(event) {
      if (event.target.scrollTop <= 500 && this.fakeScrollTop > event.target.scrollTop) {
        const arrShema = [];
        if (this.startPositionRow >= 3) {
          this.startPositionRow -= 3;
          this.countRow -= 3;
          event.target.scrollTop = event.target.scrollTop + event.target.scrollTop * 0.1;
        }
        if (this.startPositionRow < 3) {
          this.startPositionRow = 1;
          this.countRow = 100;
        }

        for (let ind = this.startPositionRow; ind < (this.countRow); ind += 1) {
          const clone = JSON.parse(JSON.stringify(this.cloneObjArrEmptyStrings));
          clone._ = ind;
          arrShema.push(clone);
        }
        this.payloadData = JSON.stringify({
          start_col: 'A',
          start_row: this.startPositionRow < 3 ? 1 : this.startPositionRow,
          end_col: 'AAAA',
          end_row: this.startPositionRow + 100,
        });
        await this.handleGeneralData(this.payloadData);
        this.updateShemaData(arrShema);
      }
      this.fakeScrollTop = event.target.scrollTop;
      this.maxHeightContent = event.target.scrollHeight

      if ((this.maxHeightContent / 2 - 500) <= this.fakeScrollTop) {
        const arrShema = [];
        this.startPositionRow += 50;
        this.countRow += 50;
        for (let ind = this.startPositionRow; ind < (this.countRow); ind += 1) {
          const clone = JSON.parse(JSON.stringify(this.cloneObjArrEmptyStrings));
          clone._ = ind;
          arrShema.push(clone);
        }
        this.getShemaTable(arrShema);
        this.updateShemaData(arrShema);
        event.target.scrollTop = event.target.scrollTop * 0.25;
      }
    },
  },
};
</script>

<style scoped>
.disableEmpty {
  color: #ffffff;
}
.goBack{
display: block;
margin-left: 42px;
}
</style>
