export default {
  setDataFromBE: (state, dataBE) => {
    state.dataBE = dataBE;
  },
  setFields: (state, data) => {
    state.fields = data;
  },
  setItems: (state, data) => {
    state.items = data;
  },
  setValueCell: (state, data) => {
    state.valueCell = data;
  },
  setShemaTable: (state, data) => {
    state.shemaTable = data;
  },
  setOneObjShemaTable: (state, data) => {
    state.shemaTable[data.i][data.key] = data.item[data.key];
  },
};
